import React, { Component } from 'react'
import Axios from 'axios';

export default class App extends Component {

  state = {
    name: [],
    isEdit: false,
  }

  componentDidMount() {
    Axios.get(`https://jsonplaceholder.typicode.com/users`)
      .then(res => {
        const name = res.data;
        // console.log(name);
        this.setState({ name });
      })
  }

  editInput = (user, e) => {
    // console.log(user);
    // console.log(e.target.id);
    // console.log(this.state);
    const newUser = this.state.name.find(function(nme) {
      // console.log(nme.id);
      if (nme.id === e.target.id) {
        console.log(nme.id);
        // this.setState({ isEdit: true, name: newUser.name });
        // return nme;
      } else {
        console.log("salah");
      }
    }
    );

  }
 
  saveInput = () => {
    const { name } = this.state;
    // console.log("name", ...name)
    this.setState({ name, isEdit: false });
  }

  handleChange = (e) => {
    // console.log(e.target.value);
    this.setState({ name: e.target.value});
  }

  handlefocus = (event) => {
    event.target.select();
  }

  render() {
    const { name } = this.state;
    // console.log(name);
    return (
      <div>
        { name.map((user) => (
          <div key={user.id}>
            {!this.state.isEdit ?
              <h1>{user.name}</h1> :
              <input onFocus={this.handlefocus} autoFocus onChange={(e) => this.handleChange(e)} type="text" value={user.name} />
            }
            {!this.state.isEdit ?
              <button onClick={(e) => this.editInput(user, e)} id={user.id}>Edit</button> :
              <button onClick={() => this.saveInput()} id={user.id}>Save</button>
            }
            <br />
            <br />
          </div>
        ))}
      </div>
    )
  }
}
